body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

body {
	background-image: url("./assets/prism/prism.png");
}

img {
	float: left;
	width: 225px;
	height: 225px;
	margin-right: 10px;
	margin-bottom: 0;
	border-radius: 6px;
	border-color: rgba(60, 60, 60, 0.5);
	border-width: 1px;
	border-style: solid;
	/* margin-top: 0; */
}

div.customCont {
	background-color: rgb(248, 249, 250);
	margin-top: 100px;
	border-radius: 2px;
	border-style: solid;
	border-color: rgba(0, 0, 0, 0.9);
	border-width: 1px;
	overflow: auto;
	padding: 15px;
	font-size: 20px;
}

#footerBar {
	margin-top: auto;
	width: 100%;
}

#wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

/* DESKTOP VIEW */
@media (min-width: 769px) {
	#mobileView {
		display: none;
	}
	#desktopView {
		display: inline;
	}
}

/* MOBILE VIEW */
@media (max-width: 768px) {
	img {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-top: 30px;
	}
	#mobileView {
		display: inline;
	}
	#desktopView {
		display: none;
	}
}

.customLink {
	color: rgb(60, 60, 60);
	background-color: rgb(230, 230, 230);
	padding: 5px 8px;
	width: max-content;
	border-radius: 3px;
	border: 1.5px solid rgb(180, 180, 180);
}

.customLink:hover {
	color: rgb(40, 40, 40);
	background-color: rgb(200, 200, 200);
}

.customLink:active {
	background-color: rgb(180, 180, 180);
}

.customNavbar {
	background-color: rgb(248, 249, 250);
}

.navbarToggleBtn::after {
	content: none;
}

h5 {
	float: right;
	text-align: center;
	margin-top: 6px;
}

.navbarToggleBtn {
	background-color: rgb(230, 230, 230);
	margin-right: 10px;
}

.navbarToggleBtn:hover {
	background-color: rgb(200, 200, 200);
}

.navbarToggleBtn:active {
	background-color: rgb(180, 180, 180);
}

.dropItem:focus {
	background-color: rgb(248, 249, 250);
}

.dropItem:hover {
	background-color: rgb(200, 200, 200);
}

.dropItem:active {
	background-color: rgb(180, 180, 180);
	color: black;
}

a {
	color: rgb(50, 100, 240);
}

.dropItem > a,
.dropItem > a:hover,
.dropItem > a:focus,
.dropItem > a:active {
	color: inherit;
	text-decoration: inherit;
}
